:root {
  --neutral-color: #fff;
  --iris-color: #4d5ae5;
  --ocean-color: #404bbf;
}

.input_button {
  justify-content: center;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

button {
  background-color: var(--iris-color);
  color: var(--neutral-color);
  text-align: center;
  letter-spacing: .04em;
  border: 1px solid var(--neutral-color);
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition-duration: .5s;
}

button:hover, button:focus {
  background-color: var(--neutral-color);
  color: var(--iris-color);
  border: 1px solid var(--ocean-color);
  transform: scale(1.05);
}

.timer {
  justify-content: center;
  gap: 25px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.value {
  font-size: 50px;
}

